*{box-sizing:border-box}html{color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4;font-size:14px}body{margin:0;background:#eee;overflow-y:scroll}.app{width:100%;max-width:60rem;margin:0 auto;padding:0.5rem;box-sizing:border-box}.app .content{display:block}.app .search{display:none}.app.-searching .content{display:none}.app.-searching .search{display:block}.header{margin-bottom:0.5rem;display:flex;flex-direction:row;border-radius:3px;overflow:hidden;box-sizing:border-box}.header a,.header button{padding:0.5rem 1rem;background:#7ea8e2;text-decoration:none !important;border:none;cursor:pointer;outline:none}.header a+a,.header a+button{border-left:1px solid #a7c2e8}.header input{flex-grow:1;border:none;padding-left:1rem;padding-right:1rem;border-color:#fff;border-width:1px 1px 1px 0;border-top-right-radius:3px;border-bottom-right-radius:3px;border-style:solid;min-width:0;color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4}.header input:focus{outline:none;border-color:#7ea8e2}.footer{text-align:center;color:#999;padding:0.5rem 0 0 0}.footer a{color:#999;text-decoration:none}.footer a:hover{text-decoration:underline}.content,.search{width:100%;box-sizing:border-box;padding:0 1rem;border:1px solid #ccc;background:#fff;border-radius:3px;overflow-x:auto}.content a,.content a:visited,.search a,.search a:visited{color:#467fcf;text-decoration:none}.content a:hover,.search a:hover{text-decoration:underline}.content a.external,.content a.external:visited,.search a.external,.search a.external:visited{color:#6e54ba}.content a.missing,.search a.missing{color:#d32f2f}.content a.current,.search a.current{font-weight:bold}.content li p,.search li p{margin:0}.content blockquote,.search blockquote{border-left:0.25rem solid rgba(0,0,0,0.07);padding-left:0.5rem;margin:0}.content pre,.search pre{padding:0.6rem;background:#f8f8f8;border-radius:3px;font-size:0.9rem}.content p code,.content li code,.search p code,.search li code{padding:0.2rem 0.4rem;background:#f8f8f8;border-radius:3px;font-size:0.9rem}.card{border:1px solid rgba(0,0,0,0.07);border-radius:3px;padding:0.5rem 1rem;margin-bottom:1rem}.card__header{display:flex;flex-direction:column;padding-bottom:0.5rem}.card__title{margin:0;font-size:1.17rem;font-weight:bold}.card__subtitle{font-size:0.875rem;color:#778592}.card__tags{padding-top:0.5rem}.card__content{border-top:1px solid rgba(0,0,0,0.07);padding:0.5rem 0}.card__content>*:first-child{margin-top:0}.card__content>*:last-child{margin-bottom:0}ul{padding-left:1.5rem}.tag{margin-right:0.5rem}.tag::before{content:'#';color:#778592;letter-spacing:2px}.content--editing{padding:0}.CodeMirror{height:auto}
